.App {
  text-align: center;
}

.App-header {
  display: -ms-inline-flexbox;
  display: inline-flex;
  -ms-flex-align: center;
      align-items: center;
  margin-top: 30px;
}

.App-header h1 {
  display: inline;
  margin: 10px 0px 10px 0px;
  padding-left: 10px;
  font-size: 50px;
}

.App-logo {
  height: 50px;
}

.sub-header {
  margin: 0px;
  padding-bottom: 10px;
  font-size: 20px;
  font-family: 'Josefin Sans', sans-serif;
  font-weight: 400;
}

@media only screen and (max-width: 600px) {
    .App-header {
        display: -ms-flexbox;
        display: flex;
        -ms-flex-wrap: wrap;
            flex-wrap: wrap;
        -ms-flex-direction: column;
            flex-direction: column;
    }

    .App-logo {
      height: 50px;
    }

    .App-header h1 {
      padding-left: 0px;
      font-size: 35px;
    }
}



